import React, { useState, useEffect } from 'react';
import { Button, Box, Typography, Paper, Select, MenuItem, FormControl, InputLabel, Slider } from '@mui/material';
import { Stage, Layer, Image, Transformer } from 'react-konva';
import useImage from 'use-image';
import { SelectChangeEvent } from '@mui/material/Select';
import { logoFiles, Logo } from '../utils/logoFiles';

function LogoEmbedder() {
  const [selectedFile, setSelectedFile] = useState<string | null>(null);
  const [selectedLogo, setSelectedLogo] = useState<string>('');
  const [logos] = useState<Logo[]>(logoFiles);
  const [logoPosition, setLogoPosition] = useState({ x: 0, y: 0 });
  const [logoScale, setLogoScale] = useState(1);
  const [stageSize, setStageSize] = useState({ width: 0, height: 0 });
  const [initialLogoScale, setInitialLogoScale] = useState(1);

  const [backgroundImage] = useImage(selectedFile || '');
  const [logoImage] = useImage(selectedLogo ? `${process.env.PUBLIC_URL}/logos/${selectedLogo}` : '');

  useEffect(() => {
    if (backgroundImage && logoImage) {
      const scale = backgroundImage.width / logoImage.width;
      setInitialLogoScale(scale);
      setLogoScale(scale);
    }
  }, [backgroundImage, logoImage]);

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files && event.target.files.length > 0) {
      const reader = new FileReader();
      reader.addEventListener('load', () => {
        setSelectedFile(reader.result as string);
        const img = document.createElement('img');
        img.onload = () => {
          setStageSize({ width: img.width, height: img.height });
        };
        img.src = reader.result as string;
      });
      reader.readAsDataURL(event.target.files[0]);
    }
  };

  const handleLogoChange = (event: SelectChangeEvent<string>) => {
    setSelectedLogo(event.target.value);
  };

  const handleExport = () => {
    const stage = document.querySelector('canvas');
    if (stage) {
      const dataURL = stage.toDataURL();
      const link = document.createElement('a');
      link.download = 'logo_embedded_image.png';
      link.href = dataURL;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  };

  const LogoImage = () => {
    const logoRef = React.useRef(null);
    const trRef = React.useRef(null);

    useEffect(() => {
      if (trRef.current && logoRef.current) {
        // @ts-ignore
        trRef.current.nodes([logoRef.current]);
        // @ts-ignore
        trRef.current.getLayer().batchDraw();
      }
    }, []);

    return (
      <>
        <Image
          image={logoImage}
          x={logoPosition.x}
          y={logoPosition.y}
          scaleX={logoScale}
          scaleY={logoScale}
          draggable
          ref={logoRef}
          onDragEnd={(e) => {
            setLogoPosition({ x: e.target.x(), y: e.target.y() });
          }}
        />
        <Transformer
          ref={trRef}
          boundBoxFunc={(oldBox, newBox) => {
            if (newBox.width < 5 || newBox.height < 5) {
              return oldBox;
            }
            return newBox;
          }}
        />
      </>
    );
  };

  return (
    <Paper elevation={3} sx={{ p: 3, mt: 3 }}>
      <Typography variant="h5" gutterBottom>
        הטמעת לוגו
      </Typography>
      <input
        accept="image/*"
        style={{ display: 'none' }}
        id="raised-button-file"
        type="file"
        onChange={handleFileChange}
      />
      <label htmlFor="raised-button-file">
        <Button variant="contained" component="span">
          בחר תמונה
        </Button>
      </label>
      {selectedFile && (
        <>
          <FormControl fullWidth sx={{ mt: 2 }}>
            <InputLabel id="logo-select-label">בחר לוגו</InputLabel>
            <Select
              labelId="logo-select-label"
              value={selectedLogo}
              label="בחר לוגו"
              onChange={handleLogoChange}
            >
              {logos.map((logo) => (
                <MenuItem key={logo.filename} value={logo.filename}>{logo.displayName}</MenuItem>
              ))}
            </Select>
          </FormControl>
          <Box sx={{ mt: 2 }}>
            <Typography>גודל הלוגו</Typography>
            <Slider
              value={logoScale}
              min={initialLogoScale * 0.1}
              max={initialLogoScale * 2}
              step={initialLogoScale * 0.05}
              onChange={(_, newValue) => setLogoScale(newValue as number)}
            />
          </Box>
          <Stage width={stageSize.width} height={stageSize.height}>
            <Layer>
              <Image image={backgroundImage} />
              {selectedLogo && <LogoImage />}
            </Layer>
          </Stage>
          <Button variant="contained" onClick={handleExport} sx={{ mt: 2 }}>
            ייצא תמונה
          </Button>
        </>
      )}
    </Paper>
  );
}

export default LogoEmbedder;