import React from 'react';
import { AppBar, Toolbar, Typography, Button, Box, useTheme, useMediaQuery } from '@mui/material';
import { Link as RouterLink } from 'react-router-dom';

function Header() {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  return (
    <AppBar position="static" color="transparent" elevation={0} sx={{ mb: 4 }}>
      <Toolbar sx={{ flexDirection: isMobile ? 'column' : 'row', gap: 2 }}>
        <Typography variant="h6" component="div" sx={{ flexGrow: 1, textAlign: isMobile ? 'center' : 'left' }}>
          ארגז כלים דיגיטלי
        </Typography>
        <Box sx={{ display: 'flex', flexDirection: isMobile ? 'column' : 'row', gap: 1 }}>
          <Button color="inherit" component={RouterLink} to="/" sx={{ color: 'primary.main' }}>
            בית
          </Button>
          <Button color="inherit" component={RouterLink} to="/pdf-embedder" sx={{ color: 'primary.main' }}>
            הטמעת PDF
          </Button>
          <Button color="inherit" component={RouterLink} to="/logo-embedder" sx={{ color: 'primary.main' }}>
            הטמעת לוגו
          </Button>
          <Button color="inherit" component={RouterLink} to="/image-resizer" sx={{ color: 'primary.main' }}>
            הקטנת תמונה
          </Button>
          <Button color="inherit" component={RouterLink} to="/file-converter" sx={{ color: 'primary.main' }}>
            המרת קבצים
          </Button>
        </Box>
      </Toolbar>
    </AppBar>
  );
}

export default Header;